export const getNextState = (state = initialState, action) => {
  console.log(action);
  console.log(state);
  switch (action.type) {
    case ACTION_TYPE.TOGGLE_IS_FETCHING:
      return {
        ...state,
        isFetching: action.isFetching
      };
    case ACTION_TYPE.LOAD_POSTS + '_SUCCESS':
      return {
        ...state,
        posts: [...action.payload.data],
        isFetching: true,
        isRedirect: false,
        isReload: false
      };
    case ACTION_TYPE.INPUT_ADD_HANDLER:
      return {
        ...state,
        post: {
          ...state.post,
          [action.name]: action.value
        }
      };
      case ACTION_TYPE.CREATE_POST:
      return {
        ...state,
        post: {
          title: '',
          body: ''
        }
      };
      case ACTION_TYPE.CREATE_POST + '_SUCCESS':
      return {
        ...state,
        isRedirect: true
      };
      case ACTION_TYPE.DELETE_POST + '_FAIL':
      return {
        ...state,
        isReload: true,
      };
    default:
      return state;
  }
};